*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
 
  color: #000;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}


img {
  display: block;
  max-width: 100%;
  height: auto;
}

.title{
    display: grid;
    place-items: center;
    font-size: 40px;
}

.cards__info{
    display: flex;
    justify-content: center;
    align-items: center;
    
    padding: 20px;

}

.cards__items{
   font-size: 30px;
margin-right: 40px;

}
